import React from "react";

const MovieList = () => {
  return (
    <div style={{ padding: "2rem" }}>
      <h2>Theatre Owner - Movie List</h2>
      <p>This page will show all movies currently managed by the theatre owner.</p>
      <ul>
        <li>Movie title</li>
        <li>Genre</li>
        <li>Showtimes</li>
        <li>Actions (Edit / Delete)</li>
      </ul>
    </div>
  );
};

export default MovieList;
